@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
.loading{
    margin-top: 3em;
    display: flex;
    flex-direction: row;
    justify-content: center;
    
    

}

.loading_msg{
    display: flex;
    flex-direction: row;
    grid-gap: 2em;
    gap: 2em;
    align-items: center;
    font-size: 2em;
    height: 2em;
}
.missions-table{
    display: flex;
    width: 100%;
    justify-content: end;
    padding-right: 2vw;

}
.missions{
    display: flex;
    width: 77%;
    flex-direction: column;
    font-size: small;
    font-weight: 600;

}

.mission-header{
    display: grid;
    grid-template-columns:  3.5fr 2.5fr 1.5fr 1.5fr 1fr;
    grid-gap: 1.2em;
    gap: 1.2em;
    margin-top: 0.5em;   
}
.mission{
    display: grid;
    grid-template-columns: 3.5fr 2.5fr 1.5fr 1.5fr 1fr;
    grid-gap: 1.2em;
    gap: 1.2em;
    margin-top: 0.5em;    
    align-items: center;
    color: var(--moon-dust);
}

.mission-header-cell{
    width: 100%;
    padding-top: 0.6em;
    padding-bottom: 0.6em;
    padding-left: 1em;
    border-radius: 1em;
    margin: 0.2em;
}

.cell{
    background-color: var(--starlight);
    width: 100%;
    padding-top: 2em;
    padding-bottom: 2em;
    padding-left: 1em;
    border-radius: 1em;
    margin: 0.1em;
}
.center{
    text-align: center;
}

.load-more{
    display: flex;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
    flex-direction: row;
    height: 1em;
    padding-left: 1em;
    margin-top: 1em;
}
.page{
    font-weight: bold;
}
.mission:hover  .cell{
    background-color: var(--martian-soil);
    color: var(--white-dwarf);
}
.mission:hover .next-icon{
    display: grid;
}

.thin_ruler{
    width: 90%;
    display: grid;
    grid-template-columns:  3.5fr 2.5fr 1.5fr 1.5fr 1fr;
    background-color: var(--void);
    height: 0.1em;
    align-content: center;

}
.fat_ruler{
    height: 0.5em;
    background-color: var(--void);
    border-radius: 1em;
}
.arrow{
    height:1em;
}

.next-icon{
    height:2em;
    justify-self: center;
    display: none;
    color: var(--martian-soil);
    border: 2px solid;
    border-color: var(--martian-soil);
    border-radius: 50%;
}
:root {
    --martian-soil: rgb(209, 75, 57);
    --void: rgb(25, 50, 71);
    --distant-star: rgb(244, 221, 181);
    --white-dwarf: rgb(255, 255, 255);
    --starlight: rgb(253, 242, 221);
    --moon-dust: rgb(96, 96, 96);
  }
  /* Hide scrollbar for Chrome, Safari and Opera */
body::-webkit-scrollbar {
  display: none;
  font-family: sans-serif;
}

/* Hide scrollbar for IE, Edge and Firefox */
body {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

  body{
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: start;
    background-color: rgb(244, 221, 181);
    background-color: var(--distant-star);
    font-family: 'Roboto', sans-serif;
    font-size: medium;
    color:rgb(25, 50, 71);
    color:var(--void);
    background-image: url(/react-coding-exercise/static/media/takeoff.31a44e1b.svg);
    background-repeat: no-repeat;
    background-size: 100%;
  }

  .btn-primary{
    display: block;
    background-color: rgb(209, 75, 57);
    background-color: var(--martian-soil);
    color: rgb(255, 255, 255);
    color: var(--white-dwarf);
    text-align: center;
    border-radius: 2em;
    border-color: transparent;
    text-transform: uppercase;
    font-weight: bold;
    padding-left: 2em;
    padding-right: 2em;
    padding-top: 0.5em;
    padding-bottom: 0.5em;
  }
.header-wrapper{
    display: flex;
    width: 100%;
    justify-content: end;
    padding-right: 2vw;
}
  .header{
    width: 77%;
    height: 6em;
    display: grid;
    grid-template-columns: 3.5fr 4fr 1.5fr 1fr;
    grid-gap: 1.2em;
    gap: 1.2em;
    margin-top: 0.5em;    
    align-items: flex-end;
    justify-content: flex-end;
    font-size: medium;
  }

  .search_input{
    height: 2em;
    border-color: transparent;
    border-radius: 2em;
    background-color: rgb(25, 50, 71);
    background-color: var(--void);
    color: rgb(253, 242, 221);
    color: var(--starlight);
    padding-left: 1em;
  }
